
import Vue from 'vue'
import { Component } from 'vue-property-decorator';
import Tabs from '@/components/Tabs.vue';
import recordTypeList from '@/constants/recordTypeList';
import dayjs from 'dayjs';
import clone from '@/lib/clone';
import Chart from '@/components/Chart.vue';
import _ from 'lodash'

@Component({
    components: { Tabs, Chart }
})
export default class Statistics extends Vue {
    mounted() {
        const div = this.$refs.chartWrapper as HTMLDivElement
        div.scrollLeft = div.scrollWidth
    }

    get keyValueList() {
        const today = new Date()
        const array = []
        for (let i = 0; i <= 29; i++) {
            const dateString = dayjs(today).subtract(i, 'day').format('YYYY-MM-DD')
            const found = _.find(this.groupedList, { title: dateString })
            array.push({ key: dateString, value: found ? found.total : 0 })
        }
        array.sort((a, b) => {
            if (a.key > b.key) {
                return 1
            } else if (a.key === b.key) {
                return 0
            } else {
                return -1
            }
        })
        return array
    }
    get chartOptions() {
        const keys = this.keyValueList.map(item => item.key)
        const values = this.keyValueList.map(item => item.value)
        return {
            grid: {
                left: 0,
                right: 0,
            },
            xAxis: {
                type: 'category',
                data: keys,
                axisTick: { alignWithLabel: true },
                axisLine: { lineStyle: { color: '#666' } },
                axisLabel: {
                    formatter: function (value: string, index: number) {
                        return value.substring(5)
                    }
                },
            },
            yAxis: {
                type: 'value',
                show: false
            },
            series: [
                {
                    symbol: 'circle',
                    symbolSize: 12,
                    itemStyle: { borderWidth: 1, color: '#666', borderColor: '#666' },
                    data: values,
                    type: 'line'
                }
            ],

            tooltip: {
                show: true, triggerOn: 'click',
                position: 'top',
                formatter: '{c}'
            }
        }
    }

    beautify(timeTitle: string) {
        const day = dayjs(timeTitle)
        const now = dayjs()
        if (day.isSame(now, 'day')) {
            return '今天'
        } else if (day.isSame(now.subtract(1, 'day'), 'day')) {
            return '昨天'
        } else if (day.isSame(now.subtract(2, 'day'), 'day')) {
            return '前天'
        } else if (day.isSame(now, 'year')) {
            return day.format('M月D日')
        } else {
            return day.format('YYYY年M月D日')
        }
    }
    tagString(tags: string[]) {
        return tags.length === 0 ? '无' : tags.join(',')
    }
    get recordList() {
        return (this.$store.state as RootState).recordList
    }
    get groupedList() {
        const { recordList } = this;

        const newList = clone(recordList).filter(r => r.type === this.type).sort((a, b) => dayjs(b.timeAt).valueOf() - dayjs(a.timeAt).valueOf())
        if (newList.length === 0) { return [] as Result };
        type Result = { title: string, total?: number, items: RecordItem[] }[]
        const result: Result = [{ title: dayjs(newList[0].timeAt).format('YYYY-MM-DD'), items: [newList[0]] }]
        for (let i = 1; i < newList.length; i++) {
            const current = newList[i]
            const last = result[result.length - 1]
            if (dayjs(last.title).isSame(dayjs(current.timeAt), 'day')) {
                last.items.push(current);
            } else {
                result.push({ title: dayjs(current.timeAt).format('YYYY-MM-DD'), items: [current] })
            }
        }
        result.forEach(group => {
            group.total = group.items.reduce((sum, item) => sum + item.amount, 0);
        })
        return result;
    }

    created() {
        this.$store.commit('fetchRecords')
    }

    type = '-';
    recordTypeList = recordTypeList;
}
